.header__dropdown-menu {
	font-size: 2.6445rem;
	font-family: OpenSans;
	user-select: none;
	cursor: pointer;
	position: relative;
	top: 40px; }

.header__dropdown-menu_open {
	border: 5px solid #fff200;
	width: 60px;
	height: 60px;
	display: block;
	background-color: transparent;
	float: right;
	text-align: center;
	line-height: 1.1;
	border-radius: 16px; }

.header__dropdown-menu-elem {
	display: inline-block;
