.header__dropdown-menu_close {
	color: #464646; }

.header__text {
	color: #fdfcfc;
	font-size: 0.8889rem;
	line-height: 1.6667rem;
	text-align: justify; }

.header__view-more, .header__view-more-center {
	width: 250px;
	height: 50px;
	background-color: #464646;
	border-radius: 2px;
	box-shadow: 1px 1.7px 4.8px 0.3px rgba(0, 0, 0, 0.25);
	font-weight: 400;
	color: #fdfcfc;
	font-size: 0.8889rem;
	margin-top: 23px;
	.fa-paper-plane {
		padding-right: 15px;
		transform: rotate(56deg) scale(1.2) translate(9px, -5px); } }

.header__view-more {
	display: none; }

.header__view-more-center {
	margin: 40px auto 0;
	display: block; }

.header__slider {
	display: none;
	position: relative;
	overflow: hidden;
	.fa-paper-plane {
		color: #464646;
		position: absolute;
		top: 84px;
		left: 268px;
		transform: rotate(-28deg) scale(1.6,-1.6);
		animation: headerFly 2s ease-in-out forwards; } }

.header__slider-track {
	background: url("../img/fly-track.png") no-repeat;
	position: absolute;
	height: 54px;
	width: 365px;
	top: 55px;
	left: -95px;
	animation: headerTrack 2s ease-in-out forwards; }

@keyframes headerTrack {
	from {
		width: 0; }
	to {
		width: 365px; } }

@keyframes headerFly {
	from {
		top: 98px;
		left: -95px;
		transform: rotate(-82deg) scale(1.6,-1.6); }
	50% {
		top: 53px;
		transform: rotate(-52deg) scale(1.6,-1.6); }
	to {
		top: 84px;
		left: 268px; } }

.header__slider-frame {
	display: block;
	margin-top: 116px;
	width: 87.2%;
	height: auto; }

.header__slider-box {
	position: absolute;
	bottom: 17.8%;
	left: 12.4%; }

.header__slide {
	width: 100%;
	height: auto; }

.header__slider-frame2 {
