	bottom: 9.2%;
	left: 62.5%;
	width: 26.2%;
	height: auto; }

.header__slider-box2 {
	position: absolute;
	bottom: 16.6%;
	left: 69.4%; }

.header__slide-vert {
	width: 100%;
	height: auto; }

.header__slider-toggle {
	color: #464646;
	font-size: 12px;
	margin-top: 28px;
	cursor: pointer;
	user-select: none;
	padding-left: 46px; }

.header__slider-toggle i:hover {
	color: #FFF200; }

.header__slider-toggle .active {
	color: #FDFCFC; }

